.navigation{
    height: 50px;
    background-color: #0a2b57;
    width: 100%;
    top: 0;
    overflow: hidden;
    position: fixed;
}
.logo{
    color:#f2fcfc;
    font-size: 24px;
    padding: 3px;
    font-weight: bold;
}

.desktop{
    list-style-type: none;
    float: right;
    color: white;
    margin-top: 10px;
}

.menu-items{
    font-size: 18px;
    display: inline;
    padding: 14px 16px;
    cursor: pointer;
}

.menu-items:hover{
    cursor: pointer;
    color: #33b0a8;
    transition: 1s;
}

.mobile{
    display: none;
}

@media only screen and (max-width: 700px){
    .logo{
        display: none;
    }
    .desktop{
        display: none;
    }
    .mobile{
        display: inline-block;
        list-style-type: none;
    }
    .mobile li{
        display: inline;
        cursor: pointer
    }
    .mobile li i{
        font-size: 30px;
        color: white;
        padding: 24px;
    }
    .logo2{
        color:#f2fcfc;
        font-size: 24px;
        padding: 3px;
        font-weight: bold;
    }
}